<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="100"></el-table-column>

        <el-table-column label="" prop="pay_status" width="100">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.pay_status === '0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>

        <el-table-column label="" prop="is_send" width="100"></el-table-column>

        <el-table-column label="" prop="create_time" width="160">
          <template slot-scope="scope">{{scope.row.create_time | dateFormat}}</template>
        </el-table-column>

        <el-table-column label="" width="140">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="light" content="" placement="top">
              <el-button
                type="primary"
                size="mini"
                icon="el-icon-edit"
                @click="showEditDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="light" content="" placement="top">
              <el-button
                type="success"
                size="mini"
                icon="el-icon-location-outline"
                @click="showProgressDialog"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 15, 20, 25]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRefs" label-width="100">
        <!--  -->
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="citydata"
            v-model="editForm.address1"
            @change="changeProvince"
            :props="{ expandTrigger: 'hover' }"
            change-on-select
          ></el-cascader>
        </el-form-item>

        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2" class="addr2Input"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="progressDialogVisible" width="50%">
      <el-timeline>
        <el-timeline-item
          v-for="(progress, index) in progressInfo"
          :type="index-1 === progressInfo.length ? 'primary':''"
          :key="index"
          :timestamp="progress.time"
          placement="top"
        >{{progress.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import citydata from "@/components/order/citydata";

export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderList: [],

      //
      editDialogVisible: false,
      editForm: {
        address1: [],
        address2: ""
      },
      editFormRules: {
        address1: [
          { required: true, message: "/", trigger: "blur" }
        ],
        address2: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      citydata: citydata,

      //
      progressDialogVisible: false,
      progressInfo: []
    };
  },
  created() {
    this.getOrderList();
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get(`orders`, {
        params: this.queryInfo
      });
      if (res.meta.status !== 200)
        return this.$message.error(``);
      this.total = res.data.total;
      this.orderList = res.data.goods;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOrderList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getOrderList();
    },

    //
    showEditDialog(row) {
      this.editDialogVisible = true;
    },
    //
    changeProvince() {},
    //
    editDialogClosed() {
      this.$refs.editFormRefs.resetFields();
    },

    //
    async showProgressDialog() {
      const { data: res } = await this.$http.get("kuaidi/1106975712662");
      if (res.meta.status !== 200)
        return this.$message.error("");

      this.progressInfo = res.data;
      console.log(this.progressInfo);
      this.progressDialogVisible = true;
    }
  }
};
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
}
.el-pagination {
  margin-top: 15px;
}
.addr2Input {
  width: 90%;
}
</style>